import React, { useEffect } from 'react';
import type { BodyProps } from '../types';
import { OverviewPage } from '../pages/OverviewPage';
import { IssueListingPage } from '../pages/IssueListingPage';
import { ElementListingPage } from '../pages/ElementListingPage';

export const Body: React.FC<BodyProps> = ({ currentPage, onNavigate, currentUrl }) => {
  // Expose a simple navigate helper on window for inner items that only have click handlers
  (window as any).webyesNavigate = onNavigate;
  
  // Clear highlights when page changes
  useEffect(() => {
    const clearHighlights = () => {
      try {
        const highlightUtils = (window as any).webyesHighlightUtils;  
        if (highlightUtils?.clearAllHighlights) {
          highlightUtils.clearAllHighlights();
        }
      } catch (error) {
        console.warn('Highlight utils not available in this context');
      }
    };
    
    clearHighlights();
  }, [currentPage]);

  const getPageTitle = () => {
    switch (currentPage) {
      case 'accessibilityIssueHeading':
        return 'Accessibility checker';
      case 'accessibilityIssueDetails':
        return 'Issue details';
      case 'overview':
      default:
        return null;
    }
  };

  const renderPageHeader = () => {
    const title = getPageTitle();
    if (!title) return null;

    // Only show back button on Element Listing page (accessibilityIssueDetails)
    // Don't show back button on Issue Listing page (accessibilityIssueHeading)
    const showBackButton = currentPage === 'accessibilityIssueDetails';

    return (
      <div className="cy:mb-0 cy:bg-white cy:text-black cy:px-4 cy:py-3 cy:flex cy:items-center cy:gap-2">
        {showBackButton && (
          <button
            className="cy:border-0 cy:bg-transparent cy:text-black cy:cursor-pointer cy:flex cy:items-center cy:justify-center cy:p-1 cy:hover:bg-gray-50 cy:rounded"
            onClick={() => onNavigate('accessibilityIssueHeading')}
            aria-label="Go back to issues"
          >
            <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
            </svg>
          </button>
        )}
        <h2 className="cy:m-0 cy:text-black cy:text-base cy:font-semibold">{title}</h2>
      </div>
    );
  };

  const renderPage = () => {
    const commonProps = { onNavigate, currentUrl };

    switch (currentPage) {
      case 'overview':
        return <OverviewPage {...commonProps} />;
      case 'accessibilityIssueHeading':
        return <IssueListingPage {...commonProps} />;
      case 'accessibilityIssueDetails':
        return <ElementListingPage {...commonProps} />;
      default:
        return <OverviewPage {...commonProps} />;
    }
  };

  return (
    <div className="cy:flex cy:flex-col cy:h-full">
      {/* Render header at Body level for connected design */}
      {renderPageHeader()}
      
      {/* Content area that fills remaining space */}
      <div className="cy:flex-1 cy:overflow-y-auto cy:min-h-0" style={{ backgroundColor: '#DCE3EE' }}>
        <div className="cy:h-full">
          {/* Render page content */}
          {renderPage()}
        </div>
      </div>
    </div>
  );
};

