import React, { useState, useEffect } from 'react';
import type { PageProps } from '../types';
import { AccessibilityChecker } from '../components/AccessibilityChecker';
import { TimeoutError } from '../components/TimeoutError';
import { useAuditStore } from '../stores/auditStore';
import { OverviewBackground } from '../components/icons/OverviewBackground';

export const OverviewPage: React.FC<PageProps> = ({ onNavigate }) => {
  const [isAnalyzing, setIsAnalyzing] = useState(true); // Start as analyzing
  const { auditResult, auditError, setAuditError, setAuditResult } = useAuditStore();

  // Monitor auditResult changes and auto-navigate when ready
  useEffect(() => {
    
    if (auditResult) {
      setIsAnalyzing(false);
      setAuditError(null); // Clear any errors on success
      onNavigate('accessibilityIssueHeading');
    } else if (!auditError && !isAnalyzing) {
      // When audit result is cleared (during re-audit), show analyzing state
      // Only if there's no error and we're not already analyzing
      setIsAnalyzing(true);
    }
  }, [auditResult, auditError, isAnalyzing, onNavigate, setAuditError]);

  // Handle retry
  const handleRetry = () => {
    // Clear error and result, show analyzing state
    setAuditError(null);
    setAuditResult(null); // Clear previous result using hook method
    setIsAnalyzing(true); // Show analyzing state
    
    // Trigger new audit with proper preparation (same as initial audit)
    if (window.webyesAuditUtils) {
      // Use runAuditWithPreparation which includes iframe ready check and delays
      const auditFunction = window.webyesAuditUtils.runAuditWithPreparation || window.webyesAuditUtils.runMultiDeviceAuditFormattedIsolated;
      
      auditFunction()
        .then((results: unknown) => {
          // Use hook methods - they're always available when component is mounted
          setAuditResult(results);
          setAuditError(null);
        })
        .catch((error: unknown) => {
          console.error('[WebYes Checker] Retry audit failed:', error);
          // Use hook methods - they're always available when component is mounted
          const errorMessage = (error instanceof Error ? error.message : String(error));
          if (errorMessage.includes('timeout') || errorMessage.includes('Timeout')) {
            setAuditError('timeout');
          } else {
            setAuditError(errorMessage);
          }
          setAuditResult(null);
          setIsAnalyzing(false);
        });
    } else {
      // If audit utils not available, show error
      setAuditError('Audit utilities not available');
      setIsAnalyzing(false);
    }
  };

  // Show timeout error screen
  if (auditError) {
    return (
      <div className="cy:relative cy:flex cy:flex-col cy:gap-6 cy:px-4 cy:py-6 cy:min-h-[500px]">
        <div className="cy:relative cy:z-10">
          <TimeoutError onRetry={handleRetry} />
        </div>
      </div>
    );
  }

  return (
    <div className="cy:relative cy:flex cy:flex-col cy:gap-6 cy:px-4 cy:py-6 cy:min-h-[500px]">
     
      
      {/* Content */}
      <div className="cy:relative cy:z-10">
        <AccessibilityChecker
          onAnalyze={() => {}} // No manual trigger needed
          isAnalyzing={isAnalyzing}
          auditResult={auditResult}
          onOpenResults={() => onNavigate('accessibilityIssueHeading')}
        />
      </div>
       <div className=" cy:flex cy:items-center cy:justify-center cy:pointer-events-none ">
        <OverviewBackground width={400} height={400} />
      </div>
    </div>
  );
};

